angular.module('app').run(['$templateCache', function($templateCache) {$templateCache.put('app/components/card/card.html','<div class="prello-card">\r\n  <h3 class="prello-card-title" ng-bind="$ctrl.title"></h3>\r\n  <p class="prello-card-description" ng-bind="$ctrl.description" ng-if="$ctrl.description"></p>\r\n\r\n  <form role="form" ng-submit="$ctrl.addDescription($ctrl.newCardDescription)" ng-if="!$ctrl.description">\r\n    <md-input-container>\r\n      <input aria-label="Type the description for your card" ng-model="$ctrl.newCardDescription" placeholder="Add description" autocomplete="off">\r\n    </md-input-container>\r\n  </form>\r\n</div>');
$templateCache.put('app/components/list/list.html','<md-card>\n  <md-card-title>\n    <md-card-title-text>\n      <span class="md-headline" ng-bind="$ctrl.title"></span>\n    </md-card-title-text>\n  </md-card-title>\n\n  <md-card-content ng-drop="true" ng-drop-success="$ctrl.onDropCard($data)">\n\n    <div class="prello-cards" ng-repeat="card in $ctrl.cards">\n      <prello-card card="card" ng-drag="true" ng-drag-data="card" ng-drag-success="$ctrl.onDragCard($data)"></prello-card>\n    </div>\n    \n  </md-card-content>\n\n  <md-card-actions layout="column">\n    <form role="form" ng-submit="$ctrl.addNewCard($ctrl.newCardName)">\n      <md-input-container>\n        <input aria-label="Type the name for your new card" ng-model="$ctrl.newCardName" placeholder="Add new card" autocomplete="off">\n      </md-input-container>\n    </form>\n  </md-card-actions>\n</md-card>');
$templateCache.put('app/pages/board/board.html','<h1 ng-bind="$ctrl.title"></h1>\r\n\r\n<md-content layout="row">\r\n\r\n  <prello-list flex="20" new="true" ng-repeat="list in $ctrl.lists" guid="list.guid"></prello-list>\r\n\r\n  <form role="form" ng-submit="$ctrl.addNewList($ctrl.newListName)" flex="20">\r\n    <md-input-container>\r\n      <input aria-label="Type the name for your new list" ng-model="$ctrl.newListName" placeholder="Add new list">\r\n    </md-input-container>\r\n  </form>\r\n\r\n</md-content>');}]);